@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100&display=swap);
body {
  margin: 0;
  height: 100vh;
  color: #353535;
  font-family: "Roboto", sans-serif;
}
#root {
  height: 100%;
}
.app {
  height: 100%;
  display: grid;
  grid-template-columns: fit-content(8ch) auto;
  /* grid-template-rows: auto auto; */
  grid-auto-flow: row;
  position: relative;
}
.app .btn-toggle {
  cursor: pointer;
  width: 35px;
  height: 35px;
  background: #353535;
  color: #fff;
  text-align: center;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  display: none;
}
.app .sidebar-btn-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}
.app .sidebar-btn-wrapper .sidebar-btn {
  padding: 1px 15px;
  border-radius: 40px;
  background: rgba(255, 255, 255, 0.05);
  color: #adadad;
  text-decoration: none;
  margin: 0 auto;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-overflow: ellipsis;
  overflow: hidden;
}
.app .sidebar-btn-wrapper .sidebar-btn span {
  margin-left: 5px;
  font-size: 13px;
}
.app .sidebar-btn-wrapper .sidebar-btn:hover {
  color: #d8d8d8;
}
.app .collapsed .sidebar-btn {
  display: inline-block;
  border-radius: 50%;
  width: 35px;
  height: 35px;
  line-height: 40px;
  padding: 0;
}
.app main {
  width: 100%;
  display: block;
  padding-bottom: 150px;
  background-color: #e6e6ff;
}

.app main header {
  margin-bottom: 20px;
}

.app .app-content {
  padding: 24px;
}

.app .social-bagdes {
  margin-top: 10px;
}
.app .social-bagdes img {
  margin: 5px;
}
.app .block {
  display: flex;
  margin-bottom: 24px;
  font-size: 14px;
  color: #545454;
}
.app .block > span {
  margin-left: 10px;
}
.app.rtl {
  direction: rtl;
  text-align: right;
}
.app.rtl header h1 {
  -webkit-transform: translateX(20px);
          transform: translateX(20px);
}
.app.rtl .block {
  direction: rtl;
}
.app.rtl .block > span {
  margin-left: 0;
  margin-right: 10px;
}
.app.rtl .sidebar-btn-wrapper .sidebar-btn span {
  margin-left: 0;
  margin-right: 5px;
}
@media (max-width: 767.98px) {
  .app {
    overflow-x: hidden;
  }
  .app header h1 {
    font-size: 24px;
  }
}
@media (max-width: 767px) {
  .app .btn-toggle {
    display: flex;
  }
  .app.rtl .btn-toggle {
    margin-left: auto;
  }
}
.badge {
  padding: 3px 6px;
  font-size: 9px;
  letter-spacing: 1px;
  border-radius: 15px;
}
.badge.red {
  color: #fff;
  background: #d63030;
}
.badge.gray {
  color: #fff;
  background: #777;
}
.badge.yellow {
  color: #000;
  background: #ffd331;
}

.sidebar-header {
  padding-top: 2.4rem;
  padding-bottom: 2.4rem;
  /* margin-bottom: 3.2rem; */
  font-weight: bold;
  /* background: #1b2330; */
  text-align: center;
}
.sidebar-header .user-email {
  font-size: 14px;
  color: darkgray;
}
.pro-sidebar {
  /* position: fixed !important; */
  width: 280px;
  max-width: 280px;
  min-width: 280px;
}
/* .main.main-collapse{
  margin-left: 60px;
}
.main{
  margin-left: 260px;
} */
@media (max-width: 768px) {
  .pro-sidebar.md {
    position: fixed;
    left: -280px;
  }
  .main {
    margin-left: 0;
  }
  .app {
    grid-template-columns: auto;
  }
  .pro-sidebar.md.rtl {
    left: auto;
    right: -280px;
  }
}

@media (max-width: 767px) {
  .app.rtl .btn-toggle {
    margin-left: 16px;
    margin-right: 0px;
  }
}
.user-image {
  height: 80px;
  width: 80px;
  object-fit: cover;
  border-radius: 50%;
  border: 7px solid #252f3e;
}

.pro-sidebar-content,
.pro-sidebar > .pro-sidebar-inner,
.pro-sidebar .pro-menu > ul > .pro-sub-menu > .pro-inner-list-item {
  background-color: #252f3e;
}

.pro-sidebar > .pro-sidebar-inner > .pro-sidebar-layout .pro-sidebar-header {
  border-bottom: 0px;
}

.pro-sidebar-header {
  position: relative;
}

.bottom-0 {
  bottom: 0;
}

.pro-sidebar
  .pro-menu.shaped
  .pro-menu-item
  > .pro-inner-item
  > .pro-icon-wrapper {
  background-color: transparent;
}

a.button-text,
.button-text:visited {
  color: white;
  text-decoration: none;
}

.topic.Inactive {
  display: none;
}
.topic.Active {
  display: block;
}
.time-text {
  font-weight: 800;
  font-size: larger;
}

.quiz {
  margin-top: 4em;
}
.list-question {
  margin-left: 8%;
}
.submit-button {
  position: absolute;
  right: 250px;
}
.paragraph {
  width: 50em;
}


.summary-bar-container {
  width: 100%;
  background-color: #ddd;
  border-radius: 15px;
  max-height: 40px;
  overflow: hidden;
}
.summary-bar {
  display: inline-block;
  padding-top: 10px;
  padding-bottom: 10px;
  color: white;
}

