.container-body {
  max-width: 1050px;
  margin: 0 auto;
  display: flex;
}

main.summary {
  max-width: 800px;
  /* background-color: #161822; */
  padding: 16px;
  border-radius: 8px;
  margin-left: 8px;
}

nav.summary {
  width: 220px;
  min-width: 220px;
  padding: 16px;
  margin: 8px;
  align-self: flex-start;
  position: -webkit-sticky;
  position: sticky;
  top: 48px;
  max-height: calc(100vh - 70px);
  overflow: auto;
}

@media screen and (max-width: 900px) {
  nav {
    display: none;
  }
}

a {
  color: #a6acc9;
  text-decoration: none;
}

li.active > a {
  color: white;
}

li > a:hover {
  color: white;
}

/*  Gives a little bit of buffer when header is navigated to */
h2,
h3 {
  padding-top: 16px;
  margin-top: -16px;
}
